<template>
  <div
    class="small-screen"
    :class="{ 'small-screen--contacts': isContactsPage }"
  >
    <div class="small-screen__bg">
      <div v-if="isMobile" class="small-screen__poster" />
      <video
        v-else
        loop
        muted
        autoplay
        preload="metadata"
        class="small-screen__video"
      >
        <source src="@/assets/video/main/3.mp4" type="video/mp4" />
      </video>
    </div>
    <div class="small-screen__heading">
      <h1>{{ title }}</h1>
      <span>сервиса MoreCargo</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SmallStartScreen',
  props: {
    title: String
  },
  data() {
    return {
      isMobile: false
    };
  },
  computed: {
    isContactsPage() {
      return this.$route.name === 'contacts';
    }
  },
  mounted() {
    this.isMobile =
      document.body.clientWidth < 960 || document.body.clientHeight < 960;
  }
};
</script>

