<template>
  <div class="main">
    <Header
      :transparent.sync="transparent"
      :mobile-icon-active.sync="mobileIconActive"
    />
    <section class="tarifs">
      <SmallStartScreen title="Тарифы" />
      <div class="tarifs-container with-shadow">
        <div class="tarifs-container__header">
          <icon name="tarifsAuto" alt="icon" size="80" class="tarifs__icon" />
          <h2 class="tarifs__subtitle">
            Основной тариф*
          </h2>
        </div>
        <div class="content">
          <p>
            Стоимость перевозки
            <br />
            Комиссия сервиса
          </p>
          <p v-for="tariff in tariffs" :key="tariff.limit">
            {{ tariff.title }}
            <br />
            {{ tariff.percent }}%
          </p>
        </div>
        <p class="comment">
          * - Взимается с перевозчиков
          <br />
          ** - Включительно
        </p>
      </div>
      <div class="tarifs__prices">
        <div class="price with-shadow">
          <div class="tarifs-container__header">
            <icon
              name="tarifsCloud"
              alt="icon"
              size="80"
              class="tarifs__icon"
            />
            <h3 class="tarifs__subtitle">
              Обновление оценки надежности контрагента
            </h3>
          </div>
          <div class="price__content">
            {{ priceReliability.value }} {{ priceReliability.measure }}.
          </div>
          <span class="tarifs__note">Стоимость одного обновления</span>
        </div>
        <div class="price with-shadow">
          <div class="tarifs-container__header">
            <icon
              name="tarifsCloud"
              alt="icon"
              size="80"
              class="tarifs__icon"
            />
            <h3 class="tarifs__subtitle">
              Обновление собственной оценки надежности
            </h3>
          </div>
          <div class="price__content">
            Бесплатно
          </div>
        </div>
        <div class="price with-shadow">
          <div class="tarifs-container__header">
            <icon name="list" alt="icon" size="80" class="tarifs__icon" />
            <h3 class="tarifs__subtitle">
              Отправка дополнительного комплекта первичной документации*
            </h3>
          </div>
          <div class="price__content">
            {{ pricePrimaryDoc.value }} {{ pricePrimaryDoc.measure }}.
          </div>
          <span class="tarifs__note">
            Взимается и с перевозчиков и с грузовладельцев
            <br />
            * - Отправка почтой России не более 15 листов
          </span>
        </div>
      </div>
    </section>
    <Footer />
  </div>
</template>

<script>
import Icon from 'common/components/Icon.vue';
import axios from 'common/helpers/httpService';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import SmallStartScreen from '@/components/start-screen/SmallStartScreen';

export default {
  head: {
    title: 'Тарифы сервиса MoreCargo',
    meta: [
      { name: 'url', content: 'https://morecargo.ru/rates' },
      {
        name: 'description',
        content:
          'Тарифы сервиса MoreCargo - подробная информация по тарифам и комиссиям сервиса. С сервисом MoreCargo поиск грузов и перевозчиков стал доступнее'
      }
    ]
  },
  components: {
    Header,
    Footer,
    Icon,
    SmallStartScreen
  },
  data() {
    return {
      transparent: false,
      mobileIconActive: false
    };
  },
  async asyncData() {
    try {
      const {
        prices: {
          tariffs,
          price_reliability: priceReliability,
          price_primary_doc: pricePrimaryDoc
        }
      } = await axios.get('/tariffs');
      const mappedTariffs = tariffs.map((tariff, i, arr) => {
        let title;
        if (i === 0) title = `до ${tariff.limit} т.р.`;
        else if (tariff.limit === 'infinity') {
          title = `от ${arr[i - 1].limit} т.р.**`;
        } else title = `от ${arr[i - 1].limit} т.р.** до ${tariff.limit} т.р.`;
        return { ...tariff, title };
      });
      return {
        tariffs: mappedTariffs,
        priceReliability,
        pricePrimaryDoc
      };
    } catch (err) {
      return {
        tariffs: [],
        priceReliability: {},
        pricePrimaryDoc: {}
      };
    }
  }
};
</script>

